.placeholder {
  position: relative;
  content: "";
  justify-self: center;

  @include vp-1365 {
    width: 364px;
    height: 228px;
  }

  @include vp-1199 {
    width: 364px;
    height: 228px;
  }

  @include vp-767 {
    width: 266px;
    height: 167px;
  }

  @include vp-319 {
    width: 320px;
    height: 167px;
  }
}

.placeholder img {
  position: relative;
  content: "";
  left: 0;
  top: 0;
  z-index: 1;

  @include vp-1365 {
    width: 364px;
    height: 228px;
  }

  @include vp-1199 {
    width: 364px;
    height: 228px;
  }

  @include vp-767 {
    width: 266px;
    height: 167px;
  }

  @include vp-319 {
    width: 320px;
    height: 167px;
  }
}

.placeholder iframe {
  position: absolute;
  content: "";
  left: 0;
  top: 0;

  @include vp-1365 {
    width: 364px;
    height: 228px;
  }

  @include vp-1199 {
    width: 364px;
    height: 228px;
  }

  @include vp-767 {
    width: 100%;
    width: 266px;
    height: 167px;
  }

  @include vp-319 {
    width: 320px;
    height: 167px;
  }
}

.placeholder::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  background-color: #14367a80;
  z-index: 2;

  @include vp-1365 {
    width: 364px;
  }

  @include vp-1199 {
    width: 364px;
  }

  @include vp-767 {
    width: 266px;
  }

  @include vp-319 {
    width: 320px;
  }
}
